import { Box, IconButton } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import EventImage from 'components/generic/EventImage'
import React, { useState, useEffect } from 'react'
import SwipeableViews from 'react-swipeable-views'
import { autoPlay } from 'react-swipeable-views-utils'
import BannerService from 'services/banner'
import ArrowBackIcon from '@material-ui/icons/ArrowBack'
import ArrowForwardIcon from '@material-ui/icons/ArrowForward'

const AutoPlaySwipeableViews = autoPlay(SwipeableViews)

const useStyles = makeStyles(theme => ({
    image: {
        position: 'absolute',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        background: 'black',
        objectFit: 'contain',
    },
    placeholderTop: {
        background: 'black',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        height: '100%',
        maxHeight: '465px',
    },
    placeholderImage: {
        width: '100%',
        height: '100%',
        maxHeight: '465px',
        objectFit: 'scale-down',
        maxWidth: '1440px',
    },
    margin: {
        marginTop: theme.spacing(0),
    },
    backButtonWrapper: {
        background: 'black',
        position: 'absolute',
        top: 0,
        left: 0,
    },
    top: {
        [theme.breakpoints.up('lg')]: {
            paddingTop: theme.breakpoints.values.lg / 2,
        },
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
    },
    arrowButton: {
        position: 'absolute',
        top: '50%',
        transform: 'translateY(-50%)',
        backgroundColor: 'rgba(0, 0, 0, 0.5)',
        color: 'white',
        zIndex: 1,
        '&:hover': {
            backgroundColor: 'rgba(0, 0, 0, 0.7)',
        },
    },
    leftArrow: {
        left: 0,
    },
    rightArrow: {
        right: 0,
    },
}))

const BannerCarousel = (event = null) => {
    const classes = useStyles()
    const [pictures, setPictures] = useState([])

    useEffect(() => {
        BannerService.getAllBanners().then(banners => {
            if (banners) setPictures(banners)
        })
    }, [])

    const [index, setIndex] = useState(0)

    const handleNext = () => {
        setIndex(prevIndex => (prevIndex + 1) % pictures.length)
    }

    const handleBack = () => {
        setIndex(
            prevIndex => (prevIndex - 1 + pictures.length) % pictures.length,
        )
    }

    return (
        <>
            <Box style={{ position: 'relative' }} className={classes.margin}>
                {/* <IconButton
                    className={`${classes.arrowButton} ${classes.leftArrow}`}
                    onClick={handleBack}
                >
                    <ArrowBackIcon />
                </IconButton> */}
                {/* <IconButton
                    className={`${classes.arrowButton} ${classes.rightArrow}`}
                    onClick={handleNext}
                >
                    <ArrowForwardIcon />
                </IconButton> */}
                <AutoPlaySwipeableViews
                    // enableKeyboardControls
                    // enableArrowKeys
                    index={index}
                    onChangeIndex={setIndex}
                    interval={8000}
                    disabled
                >
                    {pictures?.map(picture => (
                        <Box
                            key={picture._id}
                            className={classes.placeholderTop}
                        >
                            <EventImage
                                className={classes.placeholderImage}
                                publicId={picture.icon}
                                defaultImage={require('assets/images/default_cover_image.png')}
                                transformation={{
                                    width: 1440,
                                    height: 465,
                                }}
                                buttons={picture.buttons}
                            />
                        </Box>
                    ))}
                </AutoPlaySwipeableViews>
            </Box>
        </>
    )
}

export default BannerCarousel
