import React, { useEffect, useMemo, useState } from 'react'

import { Box, Dialog, Typography } from '@material-ui/core'
import { useDispatch, useSelector } from 'react-redux'
import { useParams, useHistory } from 'react-router-dom'
import { sortBy } from 'lodash-es'

import PageWrapper from 'components/layouts/PageWrapper'

import LoadingSpinner from 'components/loadingCircle'
import ProjectDetail from 'components/projects/ProjectDetail'
import * as AuthSelectors from 'redux/auth/selectors'
import * as DashboardActions from 'redux/dashboard/actions'
import * as DashboardSelectors from 'redux/dashboard/selectors'
import * as OrganiserSelectors from 'redux/organiser/selectors'
import * as OrganiserActions from 'redux/organiser/actions'
import ProjectsService from 'services/projects'
import WinnerDisplay from 'components/winnerDisplay'
// import Scoring from './Scoring'

function Winner({ event }) {
    const dispatch = useDispatch()
    const user = useSelector(AuthSelectors.idTokenData)
    const team = useSelector(DashboardSelectors.team)
    // const annotator = useSelector(DashboardSelectors.annotator)
    // const annotatorError = useSelector(DashboardSelectors.annotatorError)
    // const annotatorLoading = useSelector(DashboardSelectors.annotatorLoading)
    const rankedProjects = useSelector(OrganiserSelectors.rankedProjects)
    const projectsByRating = useSelector(DashboardSelectors.projectsByRating)
    const allProjectsMap = useSelector(OrganiserSelectors.projectsMap)
    const [selected, setSelected] = useState(false)
    const [scores, setScores] = useState([])
    const { projectSlug } = useParams()
    const history = useHistory()

    console.log(projectsByRating)
    const callUnrankedProjects = () => {
        let unrankedProjects = []
        if (projectsByRating) {
            if (rankedProjects) {
                // if (rankedProjects == 'none') {
                // } else {
                unrankedProjects = projectsByRating.filter(project => {
                    if (project.isWinner) {
                        let found = false
                        rankedProjects.forEach(rankedProject => {
                            if (rankedProject._id === project._id) {
                                found = true
                            }
                        })
                        if (!found) {
                            return true
                        }
                    }
                    return false
                })
                // }
            }
        }

        return unrankedProjects
    }
    const unrankedProjects = callUnrankedProjects()

    const audienceChoiceProjects = useMemo(() => {
        console.log('projectsByRating:', projectsByRating)

        if (
            !Array.isArray(projectsByRating) ||
            !Array.isArray(rankedProjects)
        ) {
            return { winners: [], runnersUp: [] }
        }

        // Sort by rating in descending order
        const sortedRanking = [...projectsByRating].sort(
            (a, b) => (b.rating || 0) - (a.rating || 0),
        )

        const rankedIds = new Set(rankedProjects.map(p => p._id))

        const unrankedAudienceProjects = sortedRanking.filter(
            project => !rankedIds.has(project._id),
        )

        if (unrankedAudienceProjects.length === 0) {
            return { winners: [], runnersUp: [] }
        }

        // Group by rating
        const winners = []
        const runnersUp = []

        const topRating = unrankedAudienceProjects[0].rating || 0

        // Collect all with top rating
        for (const project of unrankedAudienceProjects) {
            if ((project.rating || 0) === topRating) {
                winners.push(project)
            } else {
                break
            }
        }

        // Collect next group with second-highest rating
        const secondGroupStartIndex = winners.length
        const secondRating =
            unrankedAudienceProjects[secondGroupStartIndex]?.rating || null

        if (secondRating !== null) {
            for (
                let i = secondGroupStartIndex;
                i < unrankedAudienceProjects.length;
                i++
            ) {
                if (
                    (unrankedAudienceProjects[i].rating || 0) === secondRating
                ) {
                    runnersUp.push(unrankedAudienceProjects[i])
                } else {
                    break
                }
            }
        }

        return { winners, runnersUp }
    }, [projectsByRating, rankedProjects])

    const eventDescription = () => {
        if (projectsByRating.length === 0) {
            return (
                <p>
                    The list of winners of this hackathon will be displayed in
                    this section once the winners have been selected.
                </p>
            )
        }
    }
    const handleScoring = (projectId, score, feedback) => {
        console.log(score)
        dispatch(DashboardActions.giveScore(projectId, score, event.slug))
        dispatch(DashboardActions.giveFeedback(projectId, feedback, event.slug))
        dispatch(DashboardActions.getProjectsByRating(event.slug))
    }

    // New function to rank projects with support for multiple winners and runner-ups
    const rankProjectsByScore = projects => {
        if (!projects || projects.length === 0) {
            return { winners: [], runnerUps: [] }
        }

        // Sort projects by globalScore (rounded) and then by regionalScore as a tie-breaker
        const sortedProjects = [...projects].sort((a, b) => {
            const scoreA = Math.round((a.globalScore || 0) * 100)
            const scoreB = Math.round((b.globalScore || 0) * 100)

            if (scoreB !== scoreA) {
                return scoreB - scoreA
            }

            // Tie-breaker using regional score
            const regionalScoreA = a.regionalScore || 0
            const regionalScoreB = b.regionalScore || 0
            return regionalScoreB - regionalScoreA
        })

        const winners = []
        const runnerUps = []

        if (sortedProjects.length === 0) {
            return { winners, runnerUps }
        }

        // Find winners: all projects with the same score as the top project
        const highestScore = Math.round(
            (sortedProjects[0].globalScore || 0) * 100,
        )
        let lastWinnerIndex = -1

        for (let i = 0; i < sortedProjects.length; i++) {
            if (
                Math.round((sortedProjects[i].globalScore || 0) * 100) ===
                highestScore
            ) {
                winners.push(sortedProjects[i])
                lastWinnerIndex = i
            } else {
                break
            }
        }

        // Find runner-ups from the remaining projects
        const remainingProjects = sortedProjects.slice(lastWinnerIndex + 1)
        if (remainingProjects.length > 0) {
            const runnerUpScore = Math.round(
                (remainingProjects[0].globalScore || 0) * 100,
            )
            for (const project of remainingProjects) {
                if (
                    Math.round((project.globalScore || 0) * 100) ===
                    runnerUpScore
                ) {
                    runnerUps.push(project)
                } else {
                    break
                }
            }
        }

        return { winners, runnerUps }
    }

    const { byChallenge, byTrack } = useMemo(() => {
        if (!rankedProjects) {
            return { byChallenge: {}, byTrack: {} }
        }

        return rankedProjects.reduce(
            (res, project) => {
                console.log(project)
                if (project.track) {
                    if (res.byTrack.hasOwnProperty(project.track)) {
                        res.byTrack[project.track].push(project)
                    } else {
                        res.byTrack[project.track] = [project]
                    }
                }

                if (project.challenges) {
                    project.challenges.forEach(challenge => {
                        if (res.byChallenge.hasOwnProperty(challenge)) {
                            res.byChallenge[challenge].push(project)
                        } else {
                            res.byChallenge[challenge] = [project]
                        }
                    })
                }

                return res
            },
            {
                byChallenge: {},
                byTrack: {},
            },
        )
    }, [rankedProjects])

    let staredProjects = []

    useEffect(() => {
        if (projectsByRating && user) {
            projectsByRating.map(project => {
                // @ts-ignore
                if (user.sub) {
                    // @ts-ignore
                    if (project.usersThatRated.includes(user.sub)) {
                        staredProjects.push(project)
                    }
                }
            })
        }
    }, [projectsByRating, staredProjects, user])

    // useEffect(() => {
    //     if (projectSlug && projectsByRating) {
    //         const foundProject = projectsByRating.find(
    //             project => slugify(project.name) === projectSlug,
    //         )
    //         if (foundProject) {
    //             setSelected(foundProject)
    //         }
    //     }
    // }, [projectSlug, projectsByRating])

    useEffect(() => {
        console.log('from here', event.slug)
        dispatch(DashboardActions.getProjectsByRating(event.slug))
        dispatch(OrganiserActions.getRankedProjects(event.slug))
        dispatch(DashboardActions.updateAnnotator(event.slug))
    }, [event.slug, dispatch])

    if (!rankedProjects || !projectsByRating) {
        return <LoadingSpinner />
    }

    const slugify = name => {
        return name
            .toString()
            .toLowerCase()
            .replace(/\s+/g, '-') // Replace spaces with -
            .replace(/[^\w\-]+/g, '') // Remove all non-word chars
            .replace(/\-\-+/g, '-') // Replace multiple - with single -
            .replace(/^-+/, '') // Trim - from start of text
            .replace(/-+$/, '') // Trim - from end of text
    }

    const handleProjectSelection = project => {
        setSelected(project)
        if (project?.name) {
            const projectSlug = slugify(project.name)
            history.push(`/events/${event.slug}/project/${projectSlug}`)
        }
    }

    const handleClose = () => {
        setSelected(false)
        history.push(`/events/${event.slug}`)
    }

    return (
        <PageWrapper>
            <Box textAlign="center" paddingTop="50px">
                <h1 className="text-3xl"> Winners</h1>
            </Box>
            {eventDescription()}
            {event.tracks.map(track => {
                const items = byTrack[track.slug]
                if (!items) return null

                // Use the new ranking function instead of sorting by description length
                const { winners, runnerUps } = rankProjectsByScore(items)

                console.log('Winners:', winners)
                console.log('Runner-ups:', runnerUps)

                return (
                    <div key={track.slug}>
                        <Box
                            textAlign="center"
                            paddingTop="20px"
                            paddingBottom="20px"
                        >
                            <h1 className="text-2xl font-bold">{track.name}</h1>
                        </Box>

                        {/* Display Winners */}
                        {winners.length > 0 && (
                            <>
                                <Box textAlign="center" paddingBottom="10px">
                                    <h2 className="text-xl font-semibold">
                                         Winners
                                    </h2>
                                </Box>
                                <WinnerDisplay
                                    ranked={true}
                                    event={event}
                                    projectsByRating={winners}
                                    scores={scores}
                                    setSelected={handleProjectSelection}
                                    user={user}
                                    type="winner"
                                />
                            </>
                        )}

                        {/* Display Runner-ups */}
                        {runnerUps.length > 0 && (
                            <>
                                <Box
                                    textAlign="center"
                                    paddingTop="20px"
                                    paddingBottom="10px"
                                >
                                    <h2 className="text-xl font-semibold">
                                         Runner-ups
                                    </h2>
                                </Box>
                                <WinnerDisplay
                                    ranked={true}
                                    event={event}
                                    projectsByRating={runnerUps}
                                    scores={scores}
                                    setSelected={handleProjectSelection}
                                    user={user}
                                    type="runner-up"
                                />
                            </>
                        )}
                    </div>
                )
            })}
            {/* <h1 className="text-xl mt-6">Unranked Projects</h1>
            <RenderContent
                ranked={false}
                event={event}
                projectsByRating={unrankedProjects}
                scores={scores}
                setSelected={setSelected}
                user={user}
            /> */}
            <Dialog
                transitionDuration={0}
                fullScreen
                open={Boolean(selected)}
                onClose={handleClose}
            >
                <ProjectDetail
                    showFullTeam={true}
                    project={selected}
                    event={event}
                    onBack={handleClose}
                    showTableLocation={false}
                    fetchProject={() => {}}
                    scores={scores}
                />
            </Dialog>
            {/*  Audience Choice Section */}
            {(audienceChoiceProjects.winners.length > 0 ||
                audienceChoiceProjects.runnersUp.length > 0) && (
                <Box paddingTop="40px">
                    <Box textAlign="center">
                        <h1 className="text-2xl font-bold">
                             Audience Choice
                        </h1>
                    </Box>

                    {/* Audience Winners */}
                    {audienceChoiceProjects.winners.length > 0 && (
                        <>
                            <Box
                                textAlign="center"
                                paddingTop="20px"
                                paddingBottom="10px"
                            >
                                <h2 className="text-xl font-semibold">
                                     Winner
                                    {audienceChoiceProjects.winners.length > 1
                                        ? 's'
                                        : ''}
                                </h2>
                            </Box>
                            <WinnerDisplay
                                ranked={true}
                                event={event}
                                projectsByRating={
                                    audienceChoiceProjects.winners
                                }
                                scores={scores}
                                setSelected={handleProjectSelection}
                                user={user}
                                type="audience-winner"
                            />
                        </>
                    )}

                    {/* Audience Runners-up */}
                    {audienceChoiceProjects.runnersUp.length > 0 && (
                        <>
                            <Box
                                textAlign="center"
                                paddingTop="20px"
                                paddingBottom="10px"
                            >
                                <h2 className="text-xl font-semibold">
                                     Runner-up
                                    {audienceChoiceProjects.runnersUp.length > 1
                                        ? 's'
                                        : ''}
                                </h2>
                            </Box>
                            <WinnerDisplay
                                ranked={true}
                                event={event}
                                projectsByRating={
                                    audienceChoiceProjects.runnersUp
                                }
                                scores={scores}
                                setSelected={handleProjectSelection}
                                user={user}
                                type="audience-runner-up"
                            />
                        </>
                    )}
                </Box>
            )}
        </PageWrapper>
    )
}

export default Winner
