import React, { useState, useCallback, useEffect } from 'react'

import Upload from 'antd/es/upload'
import { useDispatch, useSelector } from 'react-redux'
import { makeStyles } from '@material-ui/core/styles'
import { Box, Typography, CircularProgress } from '@material-ui/core'
import DeleteIcon from '@material-ui/icons/Delete'
import VisibilityIcon from '@material-ui/icons/Visibility'

import * as AuthSelectors from 'redux/auth/selectors'
import * as SnackbarActions from 'redux/snackbar/actions'
import { Button, message } from 'antd'
import { CloudUploadOutlined } from '@material-ui/icons'

const useStyles = makeStyles(theme => ({
    wrapper: {
        position: 'absolute',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        background: 'gray',
    },
    emptyWrapper: {
        position: 'absolute',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        cursor: 'pointer',
    },
    emptyWrapperText: {
        textAlign: 'center',
        color: 'white',
        userSelect: 'none',
    },
    image: ({ resizeMode }) => ({
        position: 'absolute',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        objectFit: resizeMode || 'contain',
    }),
    imageOverlay: {
        position: 'absolute',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        background: 'rgba(0,0,0,0.6)',
        opacity: 0,
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        transition: 'opacity 0.2s ease',
        '&:hover': {
            opacity: 1,
        },
        cursor: 'pointer',
    },
    imageOverlayButton: {
        padding: theme.spacing(1),
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        '&:hover': {
            background: 'rgba(255,255,255,0.2)',
        },
    },
    uploader: {
        height: '100%',
        '& .ant-upload': {
            height: '100%',
        },
    },
    imageWrapper: {
        height: '100%',
    },
}))

export default ({ value, onChange, uploadUrl, resizeMode = 'contain' }) => {
    const dispatch = useDispatch()
    const idToken = useSelector(AuthSelectors.getIdToken)
    const classes = useStyles({ resizeMode })
    const [loading, setLoading] = useState(false)
    const [response, setResponse] = useState(false)
    const [fileName, setFileName] = useState('')

    useEffect(() => {
        if (response && fileName) {
            //@ts-ignore
            const newResponse = { ...response, publicId: fileName }
            onChange(newResponse)
            setLoading(false)
        }
    }, [response])

    if (!uploadUrl) {
        throw new Error(
            'DocumentUpload component must be supplied an upload url',
        )
    }

    const beforeUpload = useCallback(
        file => {
            console.log('fle type', file.name)
            setFileName(file.name)
            console.log('file name', fileName)
            const isAllowedFileType = file.type === 'application/pdf'
            // if (!isAllowedFileType) {
            //     dispatch(
            //         SnackbarActions.error('Please upload a .jpg or .png file'),
            //     )
            // }
            const isLt2M = file.size / 1024 / 1024 < 10
            if (!isLt2M) {
                dispatch(
                    SnackbarActions.error(
                        'Upload size cannot be more than 10MB',
                    ),
                )
            }
            return isAllowedFileType && isLt2M
        },
        [dispatch, fileName],
    )

    const handleChange = useCallback(
        info => {
            if (info.file.status === 'uploading') {
                setLoading(true)
                return
            }

            if (info.file.status === 'done') {
                setResponse(info.file.response)
                setLoading(false)
                // renderImage(info.file.response)
                // console.log(fileName)

                // if (fileName) {
                //     console.log('file name', fileName)

                //     response.publicId = fileName
                // } else {
                //     response.publicId = 'Untitled.pdf'
                // }
            }

            if (info.file.status === 'error') {
                const message =
                    info?.file?.response?.message ??
                    'Something went wrong... Please try again'
                dispatch(SnackbarActions.error(message))
                setLoading(false)
            }
        },
        [dispatch],
    )

    const handleRemove = useCallback(
        e => {
            e.stopPropagation()
            onChange()
        },
        [onChange],
    )

    const renderLoading = () => {
        return (
            <Box className={classes.emptyWrapper}>
                <CircularProgress size={24} style={{ color: 'white' }} />
            </Box>
        )
    }
    const renderImage = () => {
        console.log(value.url)
        return (
            <Box className={classes.imageWrapper}>
                <object
                    data={value.url}
                    type="application/pdf"
                    width="100%"
                    height="100%"
                    className="h-full min-h-[450px] overflow-hidden"
                >
                    <p>
                        Alternative text - include a link{' '}
                        <a href={value.url}>to the PDF!</a>
                    </p>
                </object>
                <Box className={classes.imageOverlay}>
                    <Box
                        className={classes.imageOverlayButton}
                        onClick={handleRemove}
                    >
                        <DeleteIcon style={{ color: 'white' }} />
                        <Box p={1} />
                        <Typography
                            variant="button"
                            style={{ color: 'white', userSelect: 'none' }}
                        >
                            Remove Document
                        </Typography>
                    </Box>
                    <Box
                        className={classes.imageOverlayButton}
                        onClick={() => window.open(value.url, '_blank')}
                    >
                        <VisibilityIcon style={{ color: 'white' }} />
                        <Box p={1} />
                        <Typography
                            variant="button"
                            style={{ color: 'white', userSelect: 'none' }}
                        >
                            View original
                        </Typography>
                    </Box>
                </Box>
            </Box>
        )
    }

    const renderEmpty = () => {
        return (
            <Box className={classes.emptyWrapper}>
                <Typography className={classes.emptyWrapperText}>
                    Click or drag a file to upload
                </Typography>
            </Box>
        )
    }

    const props = {
        name: 'file',
        action: { uploadUrl },
        headers: {
            Authorization: `Bearer ${idToken}`,
        },
        onChange(info) {
            if (info.file.status !== 'uploading') {
                console.log(info.file, info.fileList)
            }
            if (info.file.status === 'done') {
                message.success(`${info.file.name} file uploaded successfully`)
            } else if (info.file.status === 'error') {
                message.error(`${info.file.name} file upload failed.`)
            }
        },
    }
    return (
        <Box className={classes.wrapper}>
            <Upload
                name="image"
                // listType="pdf"
                className={classes.uploader}
                showUploadList={false}
                action={uploadUrl}
                headers={{
                    Authorization: `Bearer ${idToken}`,
                }}
                beforeUpload={beforeUpload}
                onChange={handleChange}
            >
                {loading && renderLoading()}
                {value && renderImage()}
                {!loading && !value && renderEmpty()}
            </Upload>
        </Box>
    )
}
