import React, { useState } from 'react'

import { makeStyles } from '@material-ui/core/styles'
import {
    Box,
    Checkbox,
    Typography,
    Button,
    CircularProgress,
} from '@material-ui/core'
import { useTranslation } from 'react-i18next'
import ExternalLink from 'components/generic/ExternalLink'
import config from 'constants/config'

const useStyles = makeStyles(theme => ({
    wrapper: {
        background: 'rgba(255,255,255,1)',
        padding: theme.spacing(2),
        marginBottom: '2px',
        width: '100%',
        maxWidth: '600px',
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
    },
    button: {
        '&.Mui-disabled': {
            backgroundColor: theme.palette.primary.main,
            opacity: 0.6,
        },
    },
}))

const SubmitButton = ({ hasErrors, loading, onSubmit, event }) => {
    const classes = useStyles()
    const [confirmed1, setConfirmed1] = useState(false)
    const [confirmed2, setConfirmed2] = useState(false)
    const [confirmed3, setConfirmed3] = useState(false)
    const [confirmed4, setConfirmed4] = useState(false)
    const { t } = useTranslation()
    const confirmed = confirmed1 && confirmed2 && confirmed3 && confirmed4
    console.log('event is ', event)
    return (
        <Box display="flex" flexDirection="column" alignItems="center">
            <Box className={classes.wrapper}>
                <Checkbox
                    color="primary"
                    className={classes.checkbox}
                    checked={confirmed1}
                    onChange={e => setConfirmed1(e.target.checked)}
                />
                <Typography variant="subtitle1" className={classes.label}>
                    {t('Consent_privacy_')}
                    <ExternalLink href={config.PRIVACY_URL}>
                        {t('Privacy_policy_', {
                            owner: config.PLATFORM_OWNER_NAME,
                        })}{' '}
                    </ExternalLink>
                    {event.eventPrivacy ? (
                        <>
                            {' and Privacy Policy of the '}
                            <ExternalLink href={event.eventPrivacy}>
                                event organiser
                            </ExternalLink>
                        </>
                    ) : null}
                </Typography>
            </Box>
            <Box className={classes.wrapper}>
                <Checkbox
                    color="primary"
                    className={classes.checkbox}
                    checked={confirmed2}
                    onChange={e => setConfirmed2(e.target.checked)}
                />
                <Typography variant="subtitle1" className={classes.label}>
                    {t('Read_agree_terms_')}
                    <ExternalLink href={config.TERMS_URL}>
                        {t('Terms_and_conditions_', {
                            owner: config.PLATFORM_OWNER_NAME,
                        })}
                    </ExternalLink>
                    {event.eventTerms ? (
                        <>
                            {' and Terms and Conditions of the '}
                            <ExternalLink href={event.eventTerms}>
                                event organiser
                            </ExternalLink>
                        </>
                    ) : null}
                </Typography>
            </Box>
            <Box className={classes.wrapper}>
                <Checkbox
                    color="primary"
                    className={classes.checkbox}
                    checked={confirmed3}
                    onChange={e => setConfirmed3(e.target.checked)}
                />
                <Typography variant="subtitle1" className={classes.label}>
                    {t('Confirm_info_')}
                </Typography>
            </Box>
            <Box className={classes.wrapper}>
                <Checkbox
                    color="primary"
                    className={classes.checkbox}
                    checked={confirmed4}
                    onChange={e => setConfirmed4(e.target.checked)}
                />
                <Typography variant="subtitle1" className={classes.label}>
                    AI Guidelines {" "}
                    <ExternalLink href="https://docs.google.com/document/d/1GNlA3I0sDpkWkkxzDlps7eHjEeSLZKu4dQENpmNdDtU/edit?tab=t.0#heading=h.owx38ytcgem9">
                        Hack Week: Third-Party AI Guidelines
                    </ExternalLink>
                </Typography>
            </Box>
            <Box
                width="100%"
                maxWidth={600}
                mt={2}
                display="flex"
                flexDirection="row"
                alignItems="center"
                justifyContent="center"
            >
                {loading ? (
                    <CircularProgress size={24} />
                ) : (
                    <Button
                        onClick={onSubmit}
                        fullWidth
                        color="primary"
                        variant="contained"
                        className={classes.button}
                        disabled={hasErrors || !confirmed || loading}
                    >
                        {t('Submit_')}
                    </Button>
                )}
            </Box>
        </Box>
    )
}

export default SubmitButton
