import React from 'react'

import { Box, TextField } from '@material-ui/core'
import { RegistrationFields } from '@hackjunction/shared'
import TextInput from 'components/inputs/TextInput'
import TextAreaInput from 'components/inputs/TextAreaInput'
import PhoneNumberInput from 'components/inputs/PhoneNumberInput'
import JobRoleInput from 'components/inputs/JobRoleInput'
import SkillsInput from 'components/inputs/SkillsInput'
import HackweekSwagField from 'components/inputs/HackweekSwag'
import DateInput from 'components/inputs/DateInput'
import FormControl from 'components/inputs/FormControl'
import Select from 'components/inputs/Select'
import EducationInput from 'components/inputs/EducationInput'
import BooleanInput from 'components/inputs/BooleanInput'
import RecruitmentOptionInput from 'components/inputs/RecruitmentOptionInput'
import TeamOptionInput from 'components/inputs/TeamOptionInput'
import hackweekSwag from '@hackjunction/shared/constants/hackweekSwag'
const { fieldTypes } = RegistrationFields
// TODO URL-input
const RegistrationQuestion = ({
    field,
    form,
    config,
    required,
    autoFocus,
    isCustom,
}) => {
    const renderInput = () => {
        switch (config.fieldType.id) {
            case fieldTypes.URL.id:
            case fieldTypes.EMAIL.id:
            case fieldTypes.SHORT_TEXT.id:
                return (
                    <FormControl
                        label={config.label}
                        error={form.errors[field.name]}
                        touched={form.touched[field.name]}
                        hint={config.hint}
                    >
                        <TextInput
                            autoFocus={autoFocus}
                            name={field.name}
                            value={field.value}
                            onChange={value =>
                                form.setFieldValue(field.name, value)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                        />
                    </FormControl>
                )
            case fieldTypes.PHONE_NUMBER.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <PhoneNumberInput
                            autoFocus={autoFocus}
                            value={field.value}
                            onChange={value =>
                                form.setFieldValue(field.name, value)
                            }
                            onBlur={value => form.setFieldTouched(field.name)}
                        />
                    </FormControl>
                )
            case fieldTypes.DATE.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <DateInput
                            autoFocus={autoFocus}
                            value={field.value}
                            onChange={date =>
                                form.setFieldValue(field.name, date)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                        />
                    </FormControl>
                )
            case fieldTypes.GENDER.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <Select
                            autoFocus={autoFocus}
                            label={config.label}
                            value={field.value}
                            onChange={gender =>
                                form.setFieldValue(field.name, gender)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                            options="gender"
                        />
                    </FormControl>
                )
            case fieldTypes.NATIONALITY.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <Select
                            autoFocus={autoFocus}
                            label={config.label}
                            value={field.value}
                            onChange={nationality =>
                                form.setFieldValue(field.name, nationality)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                            options="nationality"
                        />
                    </FormControl>
                )
            case fieldTypes.EXPERTISE.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <Select
                            autoFocus={autoFocus}
                            label=""
                            value={field.value}
                            onChange={expertise =>
                                form.setFieldValue(field.name, expertise)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                            options="expertise"
                            isMulti
                        />
                    </FormControl>
                )
            case fieldTypes.DIVISION.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <Select
                            autoFocus={autoFocus}
                            label=""
                            value={field.value}
                            onChange={division =>
                                form.setFieldValue(field.name, division)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                            options="division"
                        />
                    </FormControl>
                )
            case fieldTypes.HACKWEEKSWAG.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <Select
                            autoFocus={autoFocus}
                            label=""
                            value={field.value?.selection || field.value}
                            onChange={hackweekSwag => {
                                // Check if field.value is already structured properly
                                const isStructured =
                                    typeof field.value === 'object' &&
                                    field.value !== null &&
                                    'selection' in field.value

                                if (hackweekSwag === 'other') {
                                    // Update with structured data that includes both selection and customValue
                                    form.setFieldValue(field.name, {
                                        selection: hackweekSwag,
                                        customValue: isStructured
                                            ? field.value.customValue
                                            : '',
                                    })
                                } else {
                                    // For non-"other" options, we still use structured data but with empty customValue
                                    form.setFieldValue(field.name, {
                                        selection: hackweekSwag,
                                        customValue: '',
                                    })
                                }
                            }}
                            onBlur={() => form.setFieldTouched(field.name)}
                            options="hackweekSwag"
                        />

                        {/* Show customValue input only when "other" is selected */}
                        {(field.value?.selection === 'other' ||
                            field.value === 'other') && (
                            <div style={{ marginTop: '10px' }}>
                                <label
                                    style={{
                                        display: 'block',
                                        marginBottom: '5px',
                                        textAlign: 'left',
                                    }}
                                >
                                    Please specify:
                                </label>
                                <input
                                    type="text"
                                    style={{
                                        width: '100%',
                                        padding: '8px',
                                        fontSize: '16px',
                                        borderBottom: '1px solid #ccc',
                                        borderRadius: '4px',
                                    }}
                                    value={
                                        typeof field.value === 'object' &&
                                        field.value !== null
                                            ? field.value.customValue || ''
                                            : ''
                                    }
                                    onChange={e => {
                                        const customValue = e.target.value
                                        // Update the structured field with the new customValue
                                        form.setFieldValue(field.name, {
                                            selection: 'other',
                                            customValue: customValue,
                                        })
                                    }}
                                />
                            </div>
                        )}
                    </FormControl>
                )
            case fieldTypes.OFFICE.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <Select
                            autoFocus={autoFocus}
                            label=""
                            value={field.value}
                            onChange={office =>
                                form.setFieldValue(field.name, office)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                            options="office"
                        />
                    </FormControl>
                )
            case fieldTypes.CATEGORIES.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <Select
                            autoFocus={autoFocus}
                            label=""
                            value={field.value}
                            onChange={categories =>
                                form.setFieldValue(field.name, categories)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                            options="categories"
                        />
                    </FormControl>
                )
            case fieldTypes.HACKMODULES.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <Select
                            autoFocus={autoFocus}
                            label=""
                            value={field.value}
                            onChange={hackModule =>
                                form.setFieldValue(field.name, hackModule)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                            options="hackModule"
                        />
                    </FormControl>
                )
            case fieldTypes.T_SHIRT_SIZE.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <Select
                            autoFocus={autoFocus}
                            label={config.label}
                            value={field.value}
                            onChange={items =>
                                form.setFieldValue(field.name, items)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                            options="t-shirt-size"
                        />
                    </FormControl>
                )
            case fieldTypes.COLLABORATIONINOFFICE.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <Select
                            autoFocus={autoFocus}
                            label=""
                            value={field.value}
                            onChange={collaborationInOffice =>
                                form.setFieldValue(
                                    field.name,
                                    collaborationInOffice,
                                )
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                            options="collaborationInOffice"
                        />
                    </FormControl>
                )
            case fieldTypes.COUNTRY.id:
                return (
                    <FormControl
                        autoFocus={autoFocus}
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <Select
                            autoFocus={autoFocus}
                            label={config.label}
                            value={field.value}
                            onChange={country =>
                                form.setFieldValue(field.name, country)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                            options="country"
                        />
                    </FormControl>
                )
            // case fieldTypes.ROLES.id:
            //     return (
            //         <FormControl
            //             label={config.label}
            //             hint={config.hint}
            //             touched={form.touched[field.name]}
            //             error={form.errors[field.name]}
            //         >
            //             <JobRoleInput
            //                 autoFocus={autoFocus}
            //                 value={field.value}
            //                 onChange={roles =>
            //                     form.setFieldValue(field.name, roles)
            //                 }
            //                 onBlur={() => form.setFieldTouched(field.name)}
            //             />
            //         </FormControl>
            //     )
            case fieldTypes.SKILLS.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <SkillsInput
                            autoFocus={autoFocus}
                            value={field.value}
                            onChange={skills =>
                                form.setFieldValue(field.name, skills)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                        />
                    </FormControl>
                )
            case fieldTypes.INDUSTRIES.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <Select
                            autoFocus={autoFocus}
                            label={config.label}
                            value={field.value}
                            onChange={items =>
                                form.setFieldValue(field.name, items)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                            options="industry"
                            isMulti={true}
                        />
                    </FormControl>
                )
            case fieldTypes.THEMES.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <Select
                            autoFocus={autoFocus}
                            label={config.label}
                            value={field.value}
                            onChange={items =>
                                form.setFieldValue(field.name, items)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                            options="theme"
                            isMulti={true}
                        />
                    </FormControl>
                )
            case fieldTypes.EDUCATION.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <EducationInput
                            autoFocus={autoFocus}
                            value={field.value}
                            onChange={value =>
                                form.setFieldValue(field.name, value)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                        />
                    </FormControl>
                )
            case fieldTypes.LONG_TEXT.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <TextAreaInput
                            autoFocus={autoFocus}
                            name={field.name}
                            value={field.value}
                            onChange={value =>
                                form.setFieldValue(field.name, value)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                        />
                    </FormControl>
                )
            case fieldTypes.NUM_HACKATHONS.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <Select
                            autoFocus={autoFocus}
                            label={config.label}
                            value={field.value}
                            onChange={items =>
                                form.setFieldValue(field.name, items)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                            options="num-hackathons"
                        />
                    </FormControl>
                )
            case fieldTypes.BOOLEAN.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <BooleanInput
                            autoFocus={autoFocus}
                            value={field.value}
                            onChange={value =>
                                form.setFieldValue(field.name, value)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                        />
                    </FormControl>
                )
            case fieldTypes.DIETARY_RESTRICTIONS.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <Select
                            autoFocus={autoFocus}
                            label={config.label}
                            options="dietary-restriction"
                            value={field.value}
                            onChange={items =>
                                form.setFieldValue(field.name, items)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                            isMulti={true}
                        />
                    </FormControl>
                )
            case fieldTypes.TEAM_OPTIONS.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <TeamOptionInput
                            autoFocus={autoFocus}
                            value={field.value}
                            onChange={value =>
                                form.setFieldValue(field.name, value)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                        />
                    </FormControl>
                )
            case fieldTypes.RECRUITMENT_OPTIONS.id:
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <RecruitmentOptionInput
                            autoFocus={autoFocus}
                            value={field.value}
                            onChange={value =>
                                form.setFieldValue(field.name, value)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                        />
                    </FormControl>
                )
            default:
                return null
        }
    }

    const renderCustomInput = () => {
        switch (config.fieldType) {
            case 'text':
                console.log('config', config)
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <TextInput
                            autoFocus={autoFocus}
                            name={field.name}
                            value={field.value}
                            onChange={value =>
                                form.setFieldValue(field.name, value)
                            }
                            placeholder={config.placeholder}
                            onBlur={() => form.setFieldTouched(field.name)}
                        />
                    </FormControl>
                )
            case 'textarea':
                console.log('config', config)
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <TextAreaInput
                            autoFocus={autoFocus}
                            name={field.name}
                            value={field.value}
                            onChange={value =>
                                form.setFieldValue(field.name, value)
                            }
                            placeholder={config.placeholder}
                            onBlur={() => form.setFieldTouched(field.name)}
                        />
                    </FormControl>
                )
            case 'boolean':
                //console.log('f value', field.value)
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <BooleanInput
                            autoFocus={autoFocus}
                            value={field.value}
                            onChange={value =>
                                form.setFieldValue(field.name, value)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                        />
                    </FormControl>
                )
            case 'single-choice':
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <Select
                            autoFocus={autoFocus}
                            label={'Choose one'}
                            options={config.settings.options.map(option => ({
                                value: option,
                                label: option,
                            }))}
                            value={field.value}
                            onChange={items =>
                                form.setFieldValue(field.name, items)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                        />
                    </FormControl>
                )
            case 'multiple-choice':
                return (
                    <FormControl
                        label={config.label}
                        hint={config.hint}
                        touched={form.touched[field.name]}
                        error={form.errors[field.name]}
                    >
                        <Select
                            autoFocus={autoFocus}
                            label={'Choose many'}
                            options={config.settings.options.map(option => ({
                                value: option,
                                label: option,
                            }))}
                            value={field.value}
                            onChange={items =>
                                form.setFieldValue(field.name, items)
                            }
                            onBlur={() => form.setFieldTouched(field.name)}
                            isMulti={true}
                        />
                    </FormControl>
                )
            default:
                return null
        }
    }

    if (isCustom) {
        return <Box display="flex">{renderCustomInput()}</Box>
    } else {
        return <Box display="flex">{renderInput()}</Box>
    }
}

export default RegistrationQuestion
