import React, { useState, useEffect } from 'react'
import FormControl from '../FormControl'
import Select from '../Select'
import TextInput from '../TextInput'

const HackweekSwagField = ({ field, form, config, autoFocus }) => {
    // Track the custom value separately
    const [customValue, setCustomValue] = useState('')

    // When field.value changes, update our local state
    useEffect(() => {
        if (field.value && field.value !== 'other') {
            setCustomValue(field.value)
        }
    }, [field.value])

    return (
        <FormControl
            label={config.label}
            hint={config.hint}
            touched={form.touched[field.name]}
            error={form.errors[field.name]}
        >
            <Select
                autoFocus={autoFocus}
                label=""
                value={customValue ? 'other' : field.value}
                onChange={value => {
                    form.setFieldValue(field.name, value)
                    // Clear custom value if selection is not "other"
                    if (value !== 'other') {
                        setCustomValue('')
                    }
                }}
                onBlur={() => form.setFieldTouched(field.name)}
                options="hackweekSwag"
            />

            {field.value === 'other' && (
                <div style={{ marginTop: '10px' }}>
                    <TextInput
                        label="Please specify"
                        value={customValue}
                        onChange={value => {
                            setCustomValue(value)
                            // We store both the "other" selection and the custom value
                            form.setFieldValue(field.name, value || 'other')
                        }}
                        placeholder="Please specify"
                    />
                </div>
            )}
        </FormControl>
    )
}

export default HackweekSwagField
