import React, { useState, useCallback, useEffect } from 'react'

import { makeStyles } from '@material-ui/core/styles'
import { Box, RadioGroup, Radio, FormControlLabel } from '@material-ui/core'

const useStyles = makeStyles(theme => ({
    // @ts-ignore
    radioGroup: ({ alignCenter }) => ({
        display: 'flex',
        flexDirection: 'row',
        flexWrap: 'wrap',
        justifyContent: alignCenter ? 'center' : 'flex-start',
    }),
}))

//Boolean radio buttons are on default unchecked. If value is already defined check the box

const BooleanInput = ({ value, onChange, alignCenter = false }) => {
    const classes = useStyles({ alignCenter })
    const [isYesChecked, setYesChecked] = useState(false)
    const [isAmericasChecked, setAmericasChecked] = useState(false)
    const [isEMEAChecked, setEMEAChecked] = useState(false)
    const [isIndiaChecked, setIndiaChecked] = useState(false)
    const [isNoChecked, setNoChecked] = useState(false)

    console.log('value bool', value)

    useEffect(() => {
        if (typeof value !== 'undefined') {
            console.log('how?', value)
            if ((value = 'Americas')) {
                setAmericasChecked(true)
                setEMEAChecked(false)
                setIndiaChecked(false)
            } else if ((value = 'EMEA')) {
                setEMEAChecked(true)
                setAmericasChecked(false)
                setIndiaChecked(false)
            } else if ((value = 'India')) {
                setIndiaChecked(true)
                setEMEAChecked(false)
                setAmericasChecked(false)
            }
        }
    }, [value])

    // Probably could be done better. Value came as string for some reason, and didn't have time to debug it
    const handleChange = useCallback(
        e => {
            switch (e.target.value) {
                case 'Americas':
                    setAmericasChecked(true)
                    setEMEAChecked(false)
                    setIndiaChecked(false)
                    onChange('Americas')
                    break
                case 'EMEA':
                    setEMEAChecked(true)
                    setAmericasChecked(false)
                    setIndiaChecked(false)
                    onChange('EMEA')
                    break
                case 'India':
                    setIndiaChecked(true)
                    setEMEAChecked(false)
                    setAmericasChecked(false)
                    onChange('India')
                default:
                    console.log('error')
            }
        },
        [onChange],
    )

    return (
        <Box>
            <RadioGroup
                className={classes.radioGroup}
                aria-label="yes-no"
                value={value}
                onChange={handleChange}
            >
                <FormControlLabel
                    key={'Americas'}
                    control={<Radio color="primary" />}
                    label={'Americas'}
                    labelPlacement="start"
                    value={'Americas'}
                    checked={isAmericasChecked}
                />
                <FormControlLabel
                    key={'EMEA'}
                    control={<Radio color="primary" />}
                    label={'EMEA'}
                    labelPlacement="start"
                    value={'EMEA'}
                    checked={isEMEAChecked}
                />
                <FormControlLabel
                    key={'India'}
                    control={<Radio color="primary" />}
                    label={'APAC'}
                    labelPlacement="start"
                    value={'India'}
                    checked={isIndiaChecked}
                />
            </RadioGroup>
        </Box>
    )
}

export default BooleanInput
